<template>
  <div v-if="userLogin['role_name'] === 'admin'">
    <v-container
      id="users"
      fluid
      tag="section"
      :style="breakpointName === 'xs' || breakpointName === 'sm' ? 'padding-top: 75px' : ''"
    >
      <base-material-card
        style="margin-top: 20px"
        icon="mdi-clipboard-text"
        title="Danh sch ngi dng"
        class="px-5 py-3"
      >
          <v-card-text :class="breakpointName !== 'lg' ? 'px-0' : ''">
            <div :class="breakpointName === 'xs' ? 'mb-3' : 'd-flex mb-3'">
              <div class="mr-auto pt-2 mb-3" v-if="breakpointName === 'xs'">
                Tng s: <span style="font-weight: bold; color: green">{{totalItem}}</span> ngi dng
              </div>
              <span class="mr-auto pt-2" v-else>
                Tng s: <span style="font-weight: bold; color: green">{{totalItem}}</span> ngi dng
              </span>
              <v-btn color="#0072bc" class="mx-0" @click.stop="addMember('add')">
                <v-icon left size="22">
                  mdi-plus
                </v-icon>
                Thm ngi dng
              </v-btn>
            </div>
            <v-data-table
              :headers="headers"
              :items="items"
              :page.sync="page"
              :items-per-page="itemsPerPage"
              hide-default-footer
              class="elevation-1"
              @page-count="pageCount = $event"
              no-data-text="Khng c"
              :loading="loadingData"
              loading-text="ang ti... "
            >
            <template v-slot:item.index="{ item, index }">
              <span>{{ index + 1 }}</span>
            </template>
            <template v-slot:item.quanTriHeThong="{ item }">
              <span style="color: red" v-if="item.quanTriHeThong">Qun tr</span>
              <span style="color: blue" v-else>Cn b c s</span>
            </template>
            <template v-slot:item.khoaTaiKhoan="{ item }">
              <span style="color: red" v-if="item.khoaTaiKhoan">ang kha</span>
              <span style="color: blue" v-else>ang m</span>
            </template>
            <template v-slot:item.action="{ item }">
              <!-- <v-tooltip top>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn @click="addMember('update', item)" color="blue" text icon class="" v-bind="attrs" v-on="on">
                    <v-icon size="22">mdi-pencil</v-icon>
                  </v-btn>
                </template>
                <span>Cp nht thng tin</span>
              </v-tooltip> -->
              <v-tooltip top v-if="!item['khoaTaiKhoan']">
                <template v-slot:activator="{ on, attrs }">
                  <v-btn @click="updateStatusUser('block', item)" color="orange" text icon class="" v-bind="attrs" v-on="on">
                    <v-icon size="22">mdi-lock-check</v-icon>
                  </v-btn>
                </template>
                <span>Kha ti khon</span>
              </v-tooltip>
              <v-tooltip top v-if="item['khoaTaiKhoan']">
                <template v-slot:activator="{ on, attrs }">
                  <v-btn @click="updateStatusUser('open', item)" color="green" text icon class="" v-bind="attrs" v-on="on">
                    <v-icon size="22">mdi-lock-open-variant-outline</v-icon>
                  </v-btn>
                </template>
                <span>M kha ti khon</span>
              </v-tooltip>
              <v-tooltip top v-if="item['role'] === 'Member'">
                <template v-slot:activator="{ on, attrs }">
                  <v-btn @click="deleteUser(item)" color="red" text icon class="" v-bind="attrs" v-on="on">
                    <v-icon size="22">mdi-delete</v-icon>
                  </v-btn>
                </template>
                <span>Xa ngi dng</span>
              </v-tooltip>
            </template>
            
            </v-data-table>
            <div class="text-center mt-4">
              <v-pagination
                v-model="page"
                :length="pageCount"
              ></v-pagination>
            </div>
          </v-card-text>
      </base-material-card>
      <v-dialog
        max-width="700"
        v-model="dialogAddMember"
      >
        <v-card>
          <v-toolbar
            dark
            color="#0072bc"
          >
            <v-toolbar-title v-if="typeAction === 'add'">Thm ngi dng</v-toolbar-title>
            <v-toolbar-title v-else>Cp nht thng tin</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-toolbar-items>
              <v-btn
                icon
                dark
                @click="dialogAddMember = false"
              >
                <v-icon>mdi-close</v-icon>
              </v-btn>
            </v-toolbar-items>
          </v-toolbar>
          <v-card-text class="mt-5">
            <v-form
              ref="formAddMember"
              v-model="validFormAdd"
              lazy-validation
            >
                <v-layout wrap>
                    <v-text-field
                        class="flex xs12 md6"
                        v-if="typeAction === 'add'"
                        v-model="userInfo.TenDangNhap"
                        :rules="accountRules"
                        required
                        outlined
                        label="Tn ng nhp"
                        prepend-inner-icon="mdi-account-check-outline"
                        dense
                        clearable
                    ></v-text-field>

                    <v-text-field
                        class="flex xs12 md6 pl-2"
                        v-if="typeAction === 'add'"
                        v-model="userInfo.MatKhau"
                        :rules="passwordRules"
                        required
                        outlined
                        label="Mt khu ng nhp"
                        prepend-inner-icon="mdi-lock-check-outline"
                        dense
                        clearable
                    ></v-text-field>

                    <v-text-field
                        class="flex xs12 md6"
                        v-model="userInfo.HoVaTen"
                        :rules="nameRules"
                        required
                        outlined
                        label="Tn ngi dng"
                        prepend-inner-icon="mdi-card-account-details-outline"
                        dense
                        clearable
                    ></v-text-field>
                    <v-text-field
                        class="flex xs12 md6 pl-2"
                        v-model="userInfo.ChucDanh"
                        :rules="nameRules"
                        required
                        outlined
                        label="Chc danh"
                        prepend-inner-icon="mdi-card-account-details-outline"
                        dense
                        clearable
                    ></v-text-field>
                    <v-text-field
                        class="flex xs12 md6"
                        v-model="userInfo.SoDienThoai"
                        :rules="telNoRules"
                        required
                        outlined
                        label="S in thoi"
                        prepend-inner-icon="mdi-phone-in-talk-outline"
                        dense
                        clearable
                    ></v-text-field>

                    <v-text-field
                        class="flex xs12 md6 pl-2"
                        v-model="userInfo.Email"
                        outlined
                        label="Email"
                        prepend-inner-icon="mdi-card-account-details-outline"
                        dense
                        clearable
                    ></v-text-field>

                    <v-autocomplete
                        class="flex xs12 md12"
                        hide-no-data
                        :items="listCoSoYTe"
                        v-model="coSoYTe"
                        item-text="tenCoSo"
                        item-value="id"
                        :rules="required"
                        required
                        outlined
                        label="C s y t qun l"
                        dense
                        prepend-inner-icon="mdi-map-marker"
                    ></v-autocomplete>
                    <v-autocomplete
                        class="flex xs12 md12"
                        hide-no-data
                        :items="listDiaBan"
                        v-model="userInfo['DiaBanCoSo_ID']"
                        :disabled="!coSoYTe"
                        item-text="tenDiaBan"
                        item-value="id"
                        clearable
                        outlined
                        label="a bn c s qun l"
                        dense
                        prepend-inner-icon="mdi-map-marker"
                    ></v-autocomplete>
                    <v-checkbox
                        v-model="userInfo['QuanTriHeThong']"
                        label="QUN TR H THNG"
                        color="indigo darken-3"
                        hide-details
                        ></v-checkbox>
                </v-layout>
            </v-form>
          </v-card-text>
          <v-card-actions class="justify-end">
            
            <v-btn color="red" class="white--text mr-2" :loading="loading" :disabled="loading" @click="cancelAddMember">
              <v-icon left>
                mdi-close
              </v-icon>
              Thot
            </v-btn>
            <v-btn class="mr-2" color="#0072bc" :loading="loading" :disabled="loading" @click="submitAddMember">
              <v-icon left>
                mdi-content-save
              </v-icon>
              <span v-if="typeAction === 'add'">Thm mi</span>
              <span v-else>Cp nht</span>
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-container>
    
  </div>
  
</template>

<script>
  export default {
    name: 'Users',

    data () {
      return {
        loading: false,
        loadingData: false,
        dialogAddMember: false,
        listDiaBan: [],
        listCoSoYTe: [],
        userInfo: {
          TenDangNhap: '',
          HoVaTen: '',
          ChucDanh: '',
          SoDienThoai: '',
          Email: '',
          MatKhau: '',
          DiaBanCoSo_ID: '',
          CoSoYTe_ID: '',
          QuanTriHeThong: false,
          KhoaTaiKhoan: false
        },
        totalItem: 0,
        page: 1,
        pageCount: 0,
        itemsPerPage: 5,
        typeAction: '',
        userUpdate: '',
        items: [],
        validFormAdd: true,
        userName: '',
        coSoYTe: '',
        diaBanCoSo: '',
        nameRules: [
          v => !!v || 'Tn ngi dng l bt buc',
          v => (v && v.length <= 100) || 'Tn khng qu 100 k t',
        ],
        account: '',
        accountRules: [
          v => !!v || 'Tn ng nhp l bt buc',
          v => /^[a-zA-Z0-9]+$/.test(v) || 'Tn ng nhp gm ch hoc s',
          v => (v && v.length <= 75) || 'Tn ng nhp khng qu 75 k t',
        ],
        passWord: '',
        passwordRules: [
          v => !!v || 'Mt khu ng nhp l bt buc',
          v => (v && v.length >= 6 && v.length <= 75) || 'Mt khu t nht 6 k t',
        ],
        telNo: '',
        telNoRules: [
          v => !!v || 'S in thoi l bt buc',
          v => /^0([1-9]{1}\d{8})$/.test(v) || 'S in thoi gm 10 ch s',
        ],
        address: '',
        addressRules: [
          v => (v && v.length <= 200) || 'a ch khng qu 200 k t',
        ],
        required: [
          (value) => {
            if(String(value).trim()){
                return true
              } else {
                return 'Thng tin bt buc'
              } 
          }
        ],
        headers: [
          {
            sortable: false,
            text: 'STT',
            align: 'center',
            value: 'index'
          },
          {
            sortable: false,
            text: 'H v tn',
            align: 'center',
            value: 'hoVaTen'
          },
          {
            sortable: false,
            text: 'Tn ng nhp',
            value: 'tenDangNhap'
          },
          {
            sortable: false,
            text: 'S in thoi',
            align: 'center',
            value: 'soDienThoai'
          },
          {
            sortable: false,
            text: 'Chc danh',
            align: 'center',
            value: 'chucDanh'
          },
          {
            sortable: false,
            text: 'Loi ti khon',
            align: 'center',
            value: 'quanTriHeThong'
          },
          {
            sortable: false,
            text: 'Trng thi ti khon',
            align: 'center',
            value: 'khoaTaiKhoan'
          },
          {
            sortable: false,
            text: 'Thao tc',
            align: 'center',
            value: 'action'
          },
        ],
      }
    },
    created () {
      let vm = this
      vm.$store.commit('SET_INDEXTAB', 3)
      vm.getMembers()
      vm.getCoSoYTe()
    },
    watch: {
      coSoYTe (val) {
        this.userInfo.CoSoYTe_ID = val
        this.getDiaBanCoSo(val)
      },
    },
    computed: {
      breakpointName () {
        return this.$store.getters.getBreakpointName
      }
    },
    methods: {
      getDiaBanCoSo (val) {
        let vm = this
        let obj = vm.listCoSoYTe.find(function (item) {
          return item.maCoSo == val
        })
        let filter = {
          id: obj['id']
        }
        vm.$store.dispatch('getDiaBanCoSo', filter).then(function (result) {
          vm.listDiaBan = result ? result : []
        })
      },
      getCoSoYTe () {
        let vm = this
        let filter = {
        }
        vm.$store.dispatch('getCoSoYTe', filter).then(function (result) {
          vm.listCoSoYTe = result ? result : []
        })
      },
      getMembers () {
        let vm = this
        vm.loadingData = true
        let filter = {
          page: 0,
          size: 30
        }
        vm.$store.dispatch('getNguoiDung', filter).then(function (result) {
          vm.loadingData = false
          if (result) {
            vm.items = result.hasOwnProperty('data') ? result.data : []
            vm.totalItem = result.hasOwnProperty('total') ? result.total : []
            vm.pageCount = Math.ceil(vm.totalItem / vm.itemsPerPage)
          } else {
            vm.items = []
          }
        })
      },
      addMember (type, user) {
        let vm = this
        vm.typeAction = type
        vm.dialogAddMember = true
        if (type === 'add') {
          setTimeout(function () {
            vm.$refs.formAddMember.reset()
            vm.$refs.formAddMember.resetValidation()
          }, 200)
        } else {
          setTimeout(function () {
            vm.userUpdate = user
            vm.userInfo = {}
            vm.userInfo.HoVaTen = user.hoVaTen
            vm.userInfo.ChucDanh = user.chucDanh
            vm.userInfo.SoDienThoai = user.soDienThoai
            vm.userInfo.Email = user.email
            vm.userInfo.DiaBanCoSo_ID = user.diaBanCoSoId
            vm.userInfo.CoSoYTe_ID = user.coSoYTeId
            vm.userInfo.QuanTriHeThong = user.quanTriHeThong
            vm.$refs.formAddMember.resetValidation()
          }, 200)
        }
        
      },
      updateStatusUser (type, user) {
        let vm = this
        vm.userUpdate = user
        let filter = vm.userUpdate
        filter['block'] = type === 'open' ? false : true
        vm.$store.dispatch('changeBlockUser', filter).then(userCredential => {
          vm.$store.commit('SHOW_SNACKBAR', {
            show: true,
            text: 'Cp nht thnh cng',
            color: 'success',
          })
          vm.getMembers()
        })
      },
      deleteUser (user) {
        let vm = this
      },
      submitAddMember () {
        let vm = this
        if (vm.$refs.formAddMember.validate()) {
          if (vm.typeAction === 'add') {
            let filter = vm.userInfo
            vm.loading = true
            vm.$store.dispatch('createNguoiDung', filter).then(userCredential => {
              vm.loading = false
              vm.dialogAddMember = false
              vm.$store.commit('SHOW_SNACKBAR', {
                show: true,
                text: 'Thm ngi dng thnh cng',
                color: 'success',
              })
              vm.getMembers()
            })
            .catch((error) => {
              vm.loading = false
              let errorCode = error.code;
              let errorMessage = error.message;
              let mess = ''
              if (errorCode == 'auth/email-already-in-use') {
                mess = 'Tn ng nhp  c s dng. Vui lng s dng tn ng nhp khc.'
              } else if (errorCode == 'auth/weak-password') {
                mess = 'Mt khu qu yu'
              } else if (errorCode == 'auth/invalid-email') {
                mess = 'Tn ng nhp khng hp l'
              } else {
                mess = errorMessage
              }
              vm.$store.commit('SHOW_SNACKBAR', {
                show: true,
                text: mess,
                color: 'error',
              })
              // ..
            });
          } else {
            let filter = {
              id: vm.userUpdate.id,
              data: vm.userInfo
            }
            vm.loading = true
            vm.$store.dispatch('updateNguoiDung', filter).then(function () {
              vm.loading = false
              vm.$store.commit('SHOW_SNACKBAR', {
                show: true,
                text: 'Cp nht thnh cng',
                color: 'success',
              })
              vm.dialogAddMember = false
              vm.getMembers()
            }).catch(function () {
              vm.loading = false
              vm.$store.commit('SHOW_SNACKBAR', {
                show: true,
                text: 'Cp nht tht bi',
                color: 'error',
              })
            })
          }
          
        }
      },
      cancelAddMember () {
        let vm = this
        vm.dialogAddMember = false
      },
    },
  }
</script>
<style lang="css" scoped>
  .v-data-table-header-mobile {
    display: none !important;
  }
</style>

